import {
  appBar, buttonOutlined, itemCaseNote, itemSubject,
} from '../templates/components';
import {
  iconCheckMark,
  iconEdit,
  iconDelete,
} from '../templates/icons';

class Classroom extends HTMLElement {
  connectedCallback() {
    this.render();
    this.insertSummary();
    this.insertSubject();
    this.insertCaseNote();
    // this.handler();
  }

  render() {
    this.innerHTML = `
      <div id="classroom-container" class="m-5 lg:w-2/5 lg:mx-auto">
        ${appBar('Ruang Kelas')}
        <div id="container-classroom-summary" class="w-full p-4 mb-6 shadow-md rounded-md">
          <div class="flex justify-between items-center grow pb-4 mb-4 border-b border-[#A7E6FF]">
            <div class="w-1/2">
              <p class="mb-2">Kelas</p>
              <p class="mb-2">Jurusan</p>
              <p class="mb-2">Tahun Ajaran</p>
              <p class="mb-2">Wali Kelas</p>
              <p class="mb-2">Jumlah Siswa</p>
            </div>
            <div id="container-summary-content" class="w-1/2 text-end"></div>
          </div>
          <div class="flex justify-between items-center gap-4">
            <div class="flex items-center gap-5">
              <button id="btn-classroom-edit" class="flex items-center gap-1">
                <span>${iconEdit}</span>
                <span>Ubah</span>
              </button>
              <button id="btn-classroom-close" class="flex items-center gap-1">
                <span>${iconCheckMark(24)}</span>
                <span>Tutup</span>
              </button>
            </div>
            <button id="btn-classroom-delete" class="flex items-center gap-1">
              <span>${iconDelete}</span>
              <span>Hapus</span>
            </button>
          </div>
        </div>
        ${buttonOutlined('add-subject', 'Tambah Mata Pelajaran', 'mb-6', 'subject-form')}
        <div class="mb-6">
          <h2 class="text-xl mb-4">Mata Pelajaran</h2>
          <div id="container-subject" class="flex flex-wrap gap-3"></div>
        </div>
        <div class="mb-5">
          <h2 class="text-xl mb-4">Catatan Kasus</h2>
          <div id="container-casenote" class="flex flex-wrap gap-3"></div>
        </div>

        <div id="container-nav-page" class="flex justify-between"></div>
        <div id="container-load"></div>
      </div>
    `;
  }

  insertSummary() {
    const containerSummaryContent = this.querySelector('#container-summary-content');
    containerSummaryContent.innerHTML += `
      <p class="mb-2 font-bold">X-A</p>
      <p class="mb-2 font-bold">TITL</p>
      <p class="mb-2 font-bold">2024/2025</p>
      <p class="mb-2 font-bold">Ardi Ajah</p>
      <p class="mb-2 font-bold">26 Orang</p>
    `;
  }

  insertSubject() {
    const containerSubject = this.querySelector('#container-subject');
    try {
      containerSubject.innerHTML += itemSubject({
        id: 1,
        subject: 'Matematika',
        schedules: [
          {
            day: 1,
            startTime: '08.00',
            endTime: '09.20',
          },
          {
            day: 0,
            startTime: '00.00',
            endTime: '00.00',
          },
        ],
      });
      containerSubject.innerHTML += itemSubject({
        id: 2,
        subject: 'Fisika',
        schedules: [
          {
            day: 2,
            startTime: '08.00',
            endTime: '09.20',
          },
          {
            day: 5,
            startTime: '08.00',
            endTime: '09.20',
          },
        ],
      });
      containerSubject.innerHTML += itemSubject({
        id: 3,
        subject: 'Biologi',
        schedules: [
          {
            day: 4,
            startTime: '08.00',
            endTime: '09.20',
          },
          {
            day: 6,
            startTime: '10.40',
            endTime: '12.00',
          },
        ],
      });
    } catch (error) {
      console.log(error);
    }
  }

  insertCaseNote() {
    const containerCasenote = this.querySelector('#container-casenote');
    containerCasenote.innerHTML += itemCaseNote({
      id: 1,
      name: 'Anto',
      date: '19-09-2024',
      notes: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Ex illum sunt tempora porro architecto, itaque dolore nostrum, a similique hic corporis? Labore eligendi expedita ea quia ad quis in quam.',
    });
    containerCasenote.innerHTML += itemCaseNote({
      id: 2,
      name: 'Budi',
      date: '20-09-2024',
      notes: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Ex illum sunt tempora porro architecto, itaque dolore nostrum, a similique hic corporis? Labore eligendi expedita ea quia ad quis in quam.',
    });
  }

  // handler() {
  //   const homeContainer = this.querySelector('#home-container');
  //   const containerLoad = this.querySelector('#container-load');
  //   const requestOnlyComponent = `
  //     <div class="relative flex gap-x-3 mb-5">
  //       <div class="flex h-6 items-center">
  //         <input id="req-only" name="req-only" type="checkbox" class="h-4 w-4 rounded accent-[#3572EF] cursor-pointer">
  //       </div>
  //       <label for="req-only" class="text-[#3572EF]">Request only</label>
  //     </div>
  //   `;
  //   // HomeHandler.init({
  //   //   homeContainer,
  //   //   changeRoleBtn,
  //   //   homeUserRole,
  //   //   username,
  //   //   containerActionPayment,
  //   //   containerRequestOnly,
  //   //   month,
  //   //   year,
  //   //   requestOnlyComponent,
  //   //   containerLoad,
  //   // });
  // }
}

customElements.define('classroom-page', Classroom);
const classroom = '<classroom-page></classroom-page>';
export default classroom;
